// components/AdminStateExplorer.tsx
import React, { useState } from 'react';
import dynamic from 'next/dynamic';
import Link from 'next/link';
import type { RegionId } from './GlobeStates';
import {
  STATE_SNAPSHOTS,
  type StateSnapshot,
} from '../lib/stateSnapshots';

// Globe is client-only
const GlobeStates = dynamic(() => import('./GlobeStates'), {
  ssr: false,
  loading: () => (
    <div className="flex h-full w-full items-center justify-center text-xs text-slate-500">
      Loading 3D atlas…
    </div>
  ),
});

// Regions available on the globe
const REGION_LABELS: { id: RegionId; label: string }[] = [
  { id: 'unitedStates', label: 'United States' },
  { id: 'canada', label: 'Canada' },
  { id: 'mexico', label: 'Mexico' },
  { id: 'netherlands', label: 'Netherlands' },
];

type SelectionKind = 'region' | 'subregion';

interface SelectionState {
  kind: SelectionKind;
  regionId: RegionId;
  id: string | number | null;
  name: string | null;
}

// Simple helper for formatting numbers
const formatNumber = (n: number) =>
  n.toLocaleString(undefined, { maximumFractionDigits: 0 });

// US FIPS → postal map so snapshot keys resolve from us-atlas ids
const FIPS_TO_STATE_CODE: Record<string, string> = {
  '01': 'AL',
  '02': 'AK',
  '04': 'AZ',
  '05': 'AR',
  '06': 'CA',
  '08': 'CO',
  '09': 'CT',
  '10': 'DE',
  '11': 'DC',
  '12': 'FL',
  '13': 'GA',
  '15': 'HI',
  '16': 'ID',
  '17': 'IL',
  '18': 'IN',
  '19': 'IA',
  '20': 'KS',
  '21': 'KY',
  '22': 'LA',
  '23': 'ME',
  '24': 'MD',
  '25': 'MA',
  '26': 'MI',
  '27': 'MN',
  '28': 'MS',
  '29': 'MO',
  '30': 'MT',
  '31': 'NE',
  '32': 'NV',
  '33': 'NH',
  '34': 'NJ',
  '35': 'NM',
  '36': 'NY',
  '37': 'NC',
  '38': 'ND',
  '39': 'OH',
  '40': 'OK',
  '41': 'OR',
  '42': 'PA',
  '44': 'RI',
  '45': 'SC',
  '46': 'SD',
  '47': 'TN',
  '48': 'TX',
  '49': 'UT',
  '50': 'VT',
  '51': 'VA',
  '53': 'WA',
  '54': 'WV',
  '55': 'WI',
  '56': 'WY',
};

const resolveStateCode = (rawId: string | number | null): string | null => {
  if (rawId === null || rawId === undefined) return null;
  const trimmed = String(rawId).trim();
  if (!trimmed) return null;

  const upper = trimmed.toUpperCase();
  if (/^[A-Z]{2}$/.test(upper)) return upper;

  const fipsKey = trimmed.length === 1 ? trimmed.padStart(2, '0') : trimmed;
  if (FIPS_TO_STATE_CODE[fipsKey]) {
    return FIPS_TO_STATE_CODE[fipsKey];
  }

  return null;
};

const AdminStateExplorer: React.FC = () => {
  const [region, setRegion] = useState<RegionId>('unitedStates');
  const [viewAltitude, setViewAltitude] = useState<number | null>(null);

  // region or subregion selection
  const [selection, setSelection] = useState<SelectionState>({
    kind: 'subregion',
    regionId: 'unitedStates',
    id: 'CA',
    name: 'California',
  });

  const isUS = selection.regionId === 'unitedStates';

  // For US, we key into STATE_SNAPSHOTS using postal
  const snapshotKey =
    isUS && selection.kind === 'subregion'
      ? resolveStateCode(selection.id)
      : null;

  const activeSnapshot: StateSnapshot | null =
    snapshotKey && STATE_SNAPSHOTS[snapshotKey]
      ? STATE_SNAPSHOTS[snapshotKey]
      : null;

  const coveragePct = activeSnapshot?.coverageScore ?? 0;
  const fullCoaRate = activeSnapshot?.fullCoaRate ?? 0;

  const regionLabel =
    REGION_LABELS.find(r => r.id === selection.regionId)?.label ??
    selection.regionId;

  const title =
    selection.kind === 'region'
      ? regionLabel
      : selection.name || regionLabel;

  const detailKind =
    selection.kind === 'region'
      ? `${regionLabel} · jurisdiction snapshot`
      : selection.regionId === 'unitedStates'
      ? 'State snapshot'
      : 'Sub-jurisdiction snapshot';

  const adminHref =
    selection.kind === 'subregion' && isUS && snapshotKey
      ? `/admin?region=${selection.regionId}&sub=${snapshotKey}`
      : `/admin?region=${selection.regionId}`;

  const crumbScope = `${regionLabel.toUpperCase()} · JURISDICTION`;
  const crumbLine =
    selection.kind === 'region'
      ? `${regionLabel} — drag, hover, and click the atlas to explore coverage in different legal regimes.`
      : `${selection.name} — metallic glass tiles punched out of a spinning globe.`;

  const zoomedIn = viewAltitude !== null && viewAltitude < 1.6;

  return (
    <main className="flex min-h-screen flex-col bg-black text-slate-50">
      {/* Top crumb + region tabs */}
      <div className="mx-auto w-full max-w-6xl px-4 pt-6 pb-4">
        <div className="mb-3 flex flex-wrap items-baseline gap-3">
          <p className="text-[0.7rem] font-semibold uppercase tracking-[0.22em] text-sky-400">
            {crumbScope}
          </p>
          <p className="text-[0.75rem] text-slate-400">{crumbLine}</p>
        </div>

        <div className="flex flex-col items-center gap-3">
          <div className="inline-flex rounded-full border border-slate-700 bg-black px-1 py-1 shadow-sm shadow-slate-900">
            {REGION_LABELS.map(r => (
              <button
                key={r.id}
                type="button"
                onClick={() => {
                  setRegion(r.id);
                  setSelection({
                    kind: 'region',
                    regionId: r.id,
                    id: r.id,
                    name: r.label,
                  });
                }}
                className={
                  'rounded-full px-4 py-1.5 text-[11px] transition ' +
                  (region === r.id
                    ? 'bg-sky-500 text-white shadow-sm shadow-sky-500/40'
                    : 'text-slate-400 hover:text-slate-100')
                }
              >
                {r.label}
              </button>
            ))}
          </div>
          <div className="flex items-center gap-2 text-[11px] text-slate-500">
            <span className="inline-flex h-2 w-2 rounded-full bg-sky-400 shadow-[0_0_0_4px_rgba(56,189,248,0.40)]" />
            <span>
              Drag to spin · Hover to “lift” · Scroll to zoom · Click to drill
              into a region
            </span>
          </div>
        </div>
      </div>

      {/* Full-bleed globe with overlayed card */}
      <div className="relative flex-1 overflow-hidden">
        {/* Globe fills the center */}
        <div className="pointer-events-auto absolute inset-0 flex items-center justify-center">
          <div className="h-full w-full max-w-5xl">
            <GlobeStates
              region={region}
              onRegionChange={setRegion}
              onStateSelect={(id, name) => {
                setSelection({
                  kind: 'subregion',
                  regionId: region,
                  id,
                  name: name || null,
                });
              }}
              onViewChange={alt => setViewAltitude(alt)}
            />
          </div>
        </div>

        {/* Data card overlays, top-right */}
        {selection && (
          <section className="pointer-events-auto absolute right-6 top-20 z-20 w-full max-w-sm rounded-3xl border border-slate-700 bg-black/85 shadow-[0_22px_60px_rgba(0,0,0,0.75)] backdrop-blur">
            <header className="border-b border-slate-800 px-4 pb-2 pt-3">
              <p className="mb-1 text-[0.68rem] font-semibold uppercase tracking-[0.22em] text-sky-400">
                {detailKind}
              </p>
              <h2 className="text-lg font-semibold text-slate-50">{title}</h2>
              <p className="mt-1 text-[0.78rem] text-slate-400">
                {selection.regionId === 'unitedStates'
                  ? 'Illustrative coverage metrics while we connect live COA feeds and lab integrations.'
                  : 'National or regional coverage snapshot while we wire in live feeds for this jurisdiction.'}
              </p>
            </header>

            {/* Only show the US fake metrics panel when we have a US snapshot */}
            {isUS && activeSnapshot && (
              <div className="px-4 pb-3 pt-3">
                <dl className="mb-4 grid grid-cols-2 gap-3 text-[0.78rem]">
                  <div>
                    <dt className="text-[0.68rem] font-medium uppercase tracking-[0.16em] text-slate-500">
                      Batches tracked
                    </dt>
                    <dd className="mt-1 text-sm font-semibold text-slate-50">
                      {formatNumber(activeSnapshot.batchesTracked)}
                    </dd>
                  </div>
                  <div>
                    <dt className="text-[0.68rem] font-medium uppercase tracking-[0.16em] text-slate-500">
                      Labs reporting
                    </dt>
                    <dd className="mt-1 text-sm font-semibold text-slate-50">
                      {formatNumber(activeSnapshot.labsReporting)}
                    </dd>
                  </div>
                  <div>
                    <dt className="text-[0.68rem] font-medium uppercase tracking-[0.16em] text-slate-500">
                      Coverage score
                    </dt>
                    <dd className="mt-1 text-sm font-semibold text-slate-50">
                      {coveragePct}/100
                    </dd>
                  </div>
                  <div>
                    <dt className="text-[0.68rem] font-medium uppercase tracking-[0.16em] text-slate-500">
                      Recent recalls
                    </dt>
                    <dd className="mt-1 text-sm font-semibold text-slate-50">
                      {activeSnapshot.recentRecalls}
                    </dd>
                  </div>
                </dl>

                <div className="space-y-3">
                  <div>
                    <div className="flex items-center justify-between text-[0.7rem] text-slate-400">
                      <span className="font-medium uppercase tracking-[0.16em]">
                        Coverage
                      </span>
                      <span className="font-semibold text-slate-100">
                        {coveragePct}%
                      </span>
                    </div>
                    <div className="mt-1 h-2 rounded-full bg-slate-800">
                      <div
                        className="h-2 rounded-full bg-gradient-to-r from-emerald-400 to-sky-500 shadow-[0_0_12px_rgba(56,189,248,0.6)]"
                        style={{
                          width: `${Math.min(coveragePct, 100)}%`,
                        }}
                      />
                    </div>
                  </div>

                  <div>
                    <div className="flex items-center justify-between text-[0.7rem] text-slate-400">
                      <span className="font-medium uppercase tracking-[0.16em]">
                        Full COA coverage
                      </span>
                      <span className="font-semibold text-slate-100">
                        {fullCoaRate}%
                      </span>
                    </div>
                    <div className="mt-1 h-2 rounded-full bg-slate-800">
                      <div
                        className="h-2 rounded-full bg-gradient-to-r from-sky-400 to-indigo-500 shadow-[0_0_10px_rgba(129,140,248,0.6)]"
                        style={{
                          width: `${Math.min(fullCoaRate, 100)}%`,
                        }}
                      />
                    </div>
                  </div>

                  <p className="text-[0.7rem] text-slate-500">
                    Metrics shown are illustrative only. In production this panel
                    will be driven by live batch, lab, and recall data from your
                    CartFax deployment.
                  </p>
                </div>
              </div>
            )}

            <footer className="flex items-center justify-between gap-3 border-t border-slate-800 px-4 py-2.5">
              <Link
                href={adminHref}
                className="inline-flex items-center justify-center rounded-full bg-sky-500 px-3 py-1.5 text-[0.78rem] font-medium text-white shadow-sm shadow-sky-500/40 transition hover:bg-sky-600"
              >
                Open admin data tools
              </Link>
              <button
                type="button"
                onClick={() => {
                  setSelection(prev => ({
                    kind: 'region',
                    regionId: prev.regionId,
                    id: prev.regionId,
                    name:
                      REGION_LABELS.find(r => r.id === prev.regionId)?.label ??
                      prev.regionId,
                  }));
                }}
                className="text-[11px] text-slate-500 hover:text-slate-300"
              >
                Reset to region
              </button>
            </footer>
          </section>
        )}
      </div>

      {/* Footer still renders but main view generally fits in one screen.
          If you truly want zero scroll anywhere, you can hide the footer
          on small heights or make it overlay. */}
    </main>
  );
};

export default AdminStateExplorer;
